import './Menu.css';
import {NavLink} from 'react-router-dom';
const Menu = () => {
     
    const menuToggle = ()=>{
        
        
         document.querySelector(".menu_list").classList.toggle("active");
   
    
    }
  

    return (
        
        <>
        
            <nav>
                <div class="brand_name">GoLiver</div>
                <div class="menu_list">
                    <ul>
                        <li><NavLink exact activeClassName="activeHover" to='/'>Home</NavLink></li>
                        <li><NavLink exact activeClassName="activeHover" to='/about'>About</NavLink></li>
                        <li><NavLink exact activeClassName="activeHover" to='/weather'>Weather</NavLink></li>
               
                       
                    </ul>


                </div>
                <div class="burger" onClick={menuToggle}>
                    <div></div>
                    <div></div>
                    <div></div>
                </div>
            </nav>

        </>

    )
}


export default Menu;